/* CreateInvoice.css */
.create-invoice {
  margin-bottom: 20px;
}

.create-invoice h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.create-invoice .form-row {
  margin-bottom: 10px;
}

.create-invoice label {
  display: block;
  margin-bottom: 5px;
}

.create-invoice input[type='text'],
.create-invoice input[type='number'] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.create-invoice button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.create-invoice button:hover {
  background-color: #0056b3;
}
