/* EditClient.css */
.edit-client {
  margin-bottom: 20px;
}

.edit-client h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.edit-client .form-row {
  margin-bottom: 10px;
}

.edit-client label {
  display: block;
  margin-bottom: 5px;
}

.edit-client input[type='text'],
.edit-client input[type='number'] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.edit-client button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.edit-client button:hover {
  background-color: #0056b3;
}

.edit-client .loading {
  text-align: center;
  margin-top: 20px;
}
