/* CreateClient.css */
.create-client {
  margin-bottom: 20px;
}

.create-client h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.create-client .form-row {
  margin-bottom: 10px;
}

.create-client label {
  display: block;
  margin-bottom: 5px;
}

.create-client input[type='text'],
.create-client input[type='number'] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.create-client button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.create-client button:hover {
  background-color: #0056b3;
}

.create-client .error-message {
  color: red;
  margin-top: 10px;
}
