/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;

  background: #192529;

  color: #fff;
}

.app {
  width: 100%;
  max-width: 1024px;
  margin-inline: auto;
}

._nav-app {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1024px;
  margin-inline: auto;
  padding-block: 20px;
}

._title {
  font-size: 30px;
  margin: 0;
}

._nav-section {
  display: flex;
  gap: 10px;
}

._nav-section li {
  margin: 0;
}

._nav-section li a {
  color: #292423;
  font-size: 20px;

  background: #fff;
  padding: 5px 15px;
  border-radius: 5px;
  text-decoration: none;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

form {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type='text'],
input[type='number'] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  margin-bottom: 10px;
}

li span {
  font-weight: bold;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.loading {
  text-align: center;
  margin-top: 20px;
}
